#calcu {
    background-color: #e4942a;
    width: 225px;
    height: 270px;
    padding: 30px;
    margin: auto;
    margin-top: 150px;
    border-radius: 10px;
    border: 2px solid #FCE4EC;
    box-shadow: 0 4px 8px 0 rgba(79, 209, 249, 0.2), 0 6px 20px 0 rgba(52, 249, 236, 0.19);
}

#calcu .container {
    padding-left: 4px;
    width: 258px;
    height: 50px;
}

#calcu button {
    background-color: #f0dfd4;
    width: 40px;
    height: 40px;
    margin-right: 20px;
    margin-top: 9px;
    font-size: 18px;
    border: none;
    transition: all;
    
}
button:hover{
    letter-spacing: 3px;
    background-color: #dc5d19;
    color: aliceblue;
    box-shadow: #dc5d19 0px 7px 29px 0px;
    transform: translateY(2px);

}

button:active{
    transform: scale(0.96);
}
#calcu input {
    width: 190px;
    height: 10px;
    text-align: end;
    padding: 15px;
    font-size: 20px;
}