nav {
  background-color: rgba(225, 9, 241, 0.932);
}
nav a {
  text-decoration: none;
  color: rgb(8, 35, 1);
  padding: 10px;
  display: inline-block;
  margin-top: auto;
}
.nav a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -6px;
  width: 2px;
  background: white;
  border-radius: 5px;
  transform: scale(0);
  transition: transform 0.5s;
}
.nav a:hover::after {
  transform: scale(1);
}
