nav li {
  display: inline-block;
}
nav ul {
  margin-top: 0px;
}
.nav .roll-out-blurred-left {
  -webkit-animation: roll-out-blurred-left 0.65s
    cubic-bezier(0.755, 0.05, 0.855, 0.06) both;
  animation: roll-out-blurred-left 0.65s cubic-bezier(0.755, 0.05, 0.855, 0.06)
    both;
}
nav a:hover {
  background-color: rgb(206, 200, 200);
  transition: 0ms;
  text-transform: unset;
}

nav a:active {
    animation: roll-out-blurred-left 0.65s cubic-bezier(0.755, 0.05, 0.855, 0.06)
    both;

}

@keyframes roll-out-blurred-left {
  0% {
    transform: translateX(0) rotate(0deg);
    filter: blur(0);
    opacity: 1;
  }
  100% {
    transform: translateX(-1000px) rotate(-720deg);
    filter: blur(50px);
    opacity: 0;
  }
}
