#pagination {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
    border: 2px solid rgb(16, 236, 53);
}


#pagination.container {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #06eda0;
    color: black;
    text-transform: uppercase;
}
#pagination.container{
    background-color: #7fffd4;
    color: white;
}